@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
*, *::before, *::after{
  margin:0;
  padding:0;
  box-sizing:border-box
}
body{
  background:rgb(81, 81, 255);
  padding:60px 50px;

}
.App{
  width:90%;
  margin:0 auto;
  height:1660px;
  box-shadow: 0 0  20px rgba(0,0,0,0.25);
  border-radius:10px;
  background:rgb(255, 255, 255);
 
}
.nav{
  width:100%;
  height:100vh;
  height:55px;
  box-shadow: 0 0 12px rgba(0,0,0,0.15)

}
.nav-items-list{
  display:flex;
  justify-content: space-around;
  align-items: center;
  flex-direction:row;
  flex-wrap:wrap

}
.nav ul{
  list-style-type: none;
}
ul > li{
  margin-top:10px;
  padding:6px 7px;
  border-radius:3.5px;
  transition:0.3s;
  width:120px;
  text-align: center;
}
/* 0 */
ul > li > a.nav-link {
  text-decoration:none;
  font-family: "Roboto",sans-serif;
}
a{
  transition:0.3s;
  color:black;
  
}
li.link-tag:hover{
  background:rgb(97, 97, 251);
  color:white;
cursor:pointer;
}
li:hover a.nav-link {
  color:white;
}
.learnMoreBtn{
  background:rgb(81, 81, 255);
  border:none;
  border:1px solid rgb(32, 93, 236);
  border-radius:5px;
  padding:8px;
  cursor:pointer;
  font-family: 'Montserrat',sans-serif;
  color:white;

}
h1{
  font-family:'Montserrat',sans-serif;
  font-weight:bold;
  color:rgb(81, 81, 255);
  text-align: center;
  position:absolute;
  font-size:48px;
  top:22%;
  left:37%;
}
.year{
  color:white;
  background:rgba(81, 81, 255,0.95);
  padding:5px 18px;
  border-radius:5px;
font-size:40px;
 
}
.gallery{
  position:absolute;
  top:45%
}
.backgroundCard{
width:310px;
box-shadow: 0 0 3px rgba(0,0,0,0.3);
cursor:pointer;
padding: 30px 0;
border-radius:10px;
}
.backgroundCard:hover{
  transition:0.3s;
  transform:scale(1.05)
}
.darkContainer{
  width:1048px;
  background:#02203c;
  position:absolute;
  bottom:-40%;
}
.productCardSection{
  width:41.4%;
  height:867px;
  background:rgb(252, 83, 45);
  position:absolute;
  top:140%;
  border-radius:0 0 0 10px;
}
.moreProducts{
  width:41.6%;
  height:868px;
  background:blue;
  position:absolute;
  top:140%;
  left:49.9%;
  border-radius: 0 0 10px 0;
  overflow:hidden
}
.moreProductsSplide, .productCardContainer{
  transform:translateX(105px)
}
.giftImage{
  padding-top:10px;
  width:115px;
}
.productCard{
  width:270px;
  height:340px;
  background:rgb(255, 255, 255);
  border-radius:5px;
  margin-top:30px;
}
.productName{
  font-family:'Montserrat',sans-serif;
  font-size:15px;
}
.price{
  font-family:sans-serif
}
.button{
  width:45%;
  height:30px;
  margin:6px;
  cursor:pointer;
  font-size:17px;
}
.addToCardBtn{
  background:rgb(75, 75, 255);
}
.buyBtn{
  background:rgb(224, 217, 4)
}
.button{
  color:white;
  font-family:'Poppins',sans-serif;
  border:none;
  border-radius:4px;
}

.addToCardBtn:hover{
  background:rgb(95, 95, 254);
}
.productHeading, .moreProductHeading{
  font-family:'Roboto',sans-serif;
  text-align: center;
  width:105%;
  color:white;
}


.form > *{
  margin-top:25px;
}
.form{
  display:flex;
  justify-content:center;
  align-items:center;
  flex-direction: column;
  transform:scale(0.65);
  position:relative;
  top:1080px;
  background:#333;
  padding:35px;
  padding-top:120px;
  border-radius:15px;
}
.sendButton{
  background:rgb(0, 255, 191);
  border:none;
  padding:12px 20px;
  width:92%;
  border-radius:4px;
  cursor:pointer;
  transition:0.3s;
  font-size:15px;
  font-weight:bold;
  font-size:1.1rem;
}
.sendButton:hover{
  background:rgb(0, 228, 171);

}
.sendButton:focus{
  transform:scaleX(1.01);
}
.contactFormHeading{
  color:white;
  font-family:'Montserrat',sans-serif;
  font-weight:lighter;
  position:absolute;
  top:3%
}
input, textarea{
  outline:none;
  cursor:pointer;
  width:90%;
  background:rgba(0,0,0,0.2);
  color:white;
  border:1px solid rgb(206, 206, 206);
  border-radius:2px;
  padding-left:5px;
  transition:0.3s;
}
input{
  height:50px;
  font-family:'Poppins',sans-serif
}
input:focus, textarea:focus{
  box-shadow: 0 0 20px rgba(255,255,255,0.2);
  background:rgba(0,0,0,0.3);

}
input::placeholder, textarea::placeholder{
  color:rgba(255,255,255,0.2);
}
textarea{
  height:200px;
}
.infoInput{
  padding-left:10px;
  padding-top:5px;
}


